.marketing_IMG
{
    height: 150px;
    width: 270px;
    background-color: rgb(100, 100, 100);
    border-radius: 10px;
    overflow: hidden;
    flex-shrink: 0;
}



.marketing_IMG img
{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.marketing_DIv
{
    padding: 10px;
    display: flex;
    align-items: center;
    gap: 20px;
    transition: transform 0.3s ease; /* Smooth transition */
}

.marketing_DIv:hover {
    transform: scale(1); /* Scales the div to 1.5 times its original size */
    cursor: pointer; /* Changes the cursor */
  
  }

  .marketing_DIv:hover {
    transform: scale(1.01); /* Scales the div to 1.5 times its original size */
    cursor: pointer; /* Changes the cursor */
  
  }

.marketing_Words
{
    color: white;
    font-size: 13px;
    text-align: left;
}