.Student_portal
{
    height: 100%;
    width: 96%;
    backdrop-filter: blur(7px);
    background: rgba(0, 0, 0, 0.6);
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.9);
    overflow: hidden;
    overflow-y: scroll;
    flex-shrink: 0;
    
}

.t_ctext
{
  font-size: 12px;
  color: white;
  width: 90%;
  padding: 10px;
  margin: 0 auto;
}

.SP_margin
{
  margin-bottom: 50px;
  height: 100%;
}

.acc_BTN
{
  padding: 10px;
  border: none;
  width: 35%;
  background-color: rgb(127, 165, 255);
  color: white;
  font-weight: 700;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  transition: transform 0.3s ease; /* Smooth transition */
}

.dec_BTN
{
  padding: 10px;
  border: none;
  width: 35%;
  background-color: rgb(255, 47, 61);
  color: white;
  font-weight: 700;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  transition: transform 0.3s ease; /* Smooth transition */
}


.dec_BTN:hover {
  transform: scale(1.09); /* Scales the div to 1.5 times its original size */
  cursor: pointer; /* Changes the cursor */

}

.acc_BTN:hover {
  transform: scale(1.09); /* Scales the div to 1.5 times its original size */
  cursor: pointer; /* Changes the cursor */

}

