.gender_OPT button
{
  background-color: transparent;
  color: rgb(127, 165, 255);
  font-size: 35px;
  border: none;
  transition: transform 0.3s ease; /* Smooth transition */
}

.gender_OPT button:hover {
  transform: scale(1.05); /* Scales the div to 1.5 times its original size */
  cursor: pointer; /* Changes the cursor */

}

.gender_OPT button:hover {
  transform: scale(1.5); /* Scales the div to 1.5 times its original size */
  cursor: pointer; /* Changes the cursor */

}

.phoneMpesa {
  animation: scale-animation 1s infinite;
}

@keyframes scale-animation {
  0% {
    transform: scale(0.9);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.9); /* Adjust the final scale as needed */
  }
}

